.common-flex {
    margin-bottom: 0.24rem;
    display: flex;
    justify-content: space-between;
    border-radius: 6px;
}

.common-flex::after {
    content: '';
    display: block;
    clear: both;
}

.common-flex {
    &:last-child {
        margin-bottom: 0;
    }
}

.commpage {
    width: 100%;
    overflow: hidden;

    ::v-deep .el-card__header {
        padding: 16px 24px 16px 40px;
    }

    ::v-deep .el-card__header {
        position: relative;
    }

    ::v-deep .el-card__header::after {
        content: "";
        position: absolute;
        background: #2e4af7;
        left: 24px;
        top: 18px;
        width: 6px;
        height: 20px;
    }

    .clearfix {
        color: #333333;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 16px;
    }

    ::v-deep .el-card__body {
        padding: 0;
    }
}

.clearfix>.peak-select {
    width: 54px;
    height: 22px;
}

.clearfix>.monitor-select {
    width: 142px;
    height: 22px;
}

.monitor-select {
    ::v-deep .el-select {
        display: block;
    }
}

.peak-select,
.monitor-select {
    ::v-deep .el-input__inner {
        height: 22px;
        line-height: 20px;
        // padding: 0 8px;
        padding-left: 8px;
        font-size: inherit;
        background: #e6e9fe;
        color: #2E4AF7;
        border: 1px solid #2E4AF7;
        display: inline-block;
        /* max-width: 180px; */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    ;

    ::v-deep .el-input__inner::-webkit-input-placeholder {}

    ::v-deep .el-input__icon {
        line-height: 22px;
        width: 20px;
        color: #2E4AF7;
    }

    ;

    ::v-deep .el-icon-arrow-up:before {
        content: "";
        width: 0;
        top: 6px;
        position: absolute;
        height: 0;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-bottom: 8px solid #2E4AF7;
        right: 9px;
    }


}

::v-deep .el-select-dropdown__item.selected {
    color: #2E4AF7;
}


.flex-big {
    width: 5.86rem;
    margin-right: 23px;
    float: left;
}

.flex-small {
    width: 4.76rem;
    margin-right: 23px;
    float: left;
}

.flex-small:last-child {
    margin-right: 0;
}

.flex-max {
    width: 9.77rem;
    float: left;
}

.light-color {
    color: #909CB1;
}

.black-color {
    color: #333333;
}


.card-box {

    position: relative;
    height: 4.05rem;
    justify-content: space-between;
    flex-flow: wrap;
    box-sizing: border-box;
}

.plaf-box,
.home-card-box {
    display: flex;
    position: relative;
    height: 4.05rem;
    padding: 0.35rem 0.25rem;
    justify-content: space-between;
    flex-flow: wrap;
    box-sizing: border-box;

}


.card-item {
    margin-bottom: 15px;
}

.nobottom {
    margin-bottom: 0;
}

.card-group {
    display: flex;
    margin-top: 0.06rem;
    width: 2.58rem;
    height: 1.3rem;
    align-items: center;
    border: 1px solid #eeeeee;
    border-radius: 6px;
    flex-flow: wrap;
}

.card-group .battery {
    width: 0.68rem;
    height: 0.68rem;
    margin: 0 0.16rem 0 0.16rem;
}

.monitor {
    width: 0.17rem;
    height: 0.17rem;
    vertical-align: middle;
    margin-right: 0.19rem;
}

.uab-box {
    // padding-top: 0.47rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.load-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}



.uab-item {
    width: 4.24rem;
    margin-bottom: 0.21rem;
    box-sizing: border-box;
    height: 0.78rem;
    display: flex;
    align-items: center;
    padding: 0 22px 0 30px;
    justify-content: space-between;
    border-radius: 4px;
    border: 1px solid #eeeeee;

    .black-color {
        display: block;
    }
}

.uab-item-title {
    font-size: 0.17rem;
}

.uab-item:last-child {
    margin-bottom: 0;
}

.uab-item-cont {
    min-width: 90px;
    text-align: left;
}

.load-item {
    width: 100%;
    margin-bottom: 0.08rem;
    box-sizing: border-box;
    height: 0.78rem;
    display: flex;
    align-items: center;
    padding: 0 22px 0 0.05rem;
    justify-content: space-between;
    border-radius: 4px;
    border: 1px solid #eeeeee;

    .black-color {
        display: block;
    }
}

.load-item .loadSvg {
    width: 0.68rem;
    height: 0.68rem;
    margin-right: 0.29rem;
}

.load-item div:nth-child(2) {
    min-width: 90px;
    text-align: left;
}

.load-item>div,
.uab-item>div {
    display: flex;
    align-items: center;
}


.card-bot {
    display: flex;
    height: 4.05rem;
    // padding: 0.35rem 0.24rem 0 0.24rem;
    justify-content: space-between;
    flex-flow: wrap;
    box-sizing: border-box;
}

.card-jc {
    display: flex;
    height: 4.05rem;
    padding: 0.35rem 0.25rem;
    justify-content: space-between;
    flex-flow: wrap;
    box-sizing: border-box;

}

.peak-box {
    height: 4.05rem;
    display: flex;
    position: relative;
    box-sizing: border-box;
}

.pers-peck-left {
    width: 200px;
    padding: 0 0.2rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.peck-left,
.peck-right {
    box-sizing: border-box;
    flex: 1;
}

.peck-left {
    padding: 0 0.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.peck-right {
    padding: 0.53rem 0.23rem 0.49rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.peck-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.peak-pie {
    width: 1.64rem;
    height: 1.64rem;
}

.trendTag {
    position: absolute;
    font-size: 0.14rem;
    color: #333;
    top: 35px;
    right: 25px;
    z-index: 2000;
}

.trendTag span {
    position: relative;
    padding-left: 15px;
}

.trendTag>span:nth-child(1) {
    margin-right: 0.28rem;
}

.trendTag>span:nth-child(1)::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0.05rem;
    border-radius: 50%;
    width: 0.1rem;
    height: 0.1rem;
    background: #ff8c56;
}

.trendTag>span:nth-child(2)::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0.05rem;
    width: 0.1rem;
    height: 0.1rem;
    border-radius: 50%;
    background: #ef4474;
}

.select-boxs {
    display: flex;
    width: 300px;
    justify-content: space-between;

}

.select-boxs .monitor-select {
    margin-left: 10px;
}

.jfitem {
    text-align: left;
    min-width: 135px;
}