.platform {
    .tabs-div {
        max-width: 50%;
        overflow: hidden;

        /deep/ .el-tabs__nav-next,
        /deep/ .el-tabs__nav-prev {
            line-height: 0.23rem;
        }

        /deep/ .el-tabs__nav-wrap {
            overflow: hidden;
        }

        /deep/ .el-tabs__item {
            height: 22px;
            line-height: 22px
        }

        /deep/ .el-tabs__nav-wrap::after {
            height: 0;
        }

        /deep/ .el-tabs__item {
            padding: 0 10px;
        }

        /deep/ .el-tabs__item {
            &:last-child {
                padding-right: 0 !important
            }
        }
    }

    .top-card {
        margin-bottom: 24px;
    }

    .paltanaly-card {
        padding: 22px 20px 33px 21px;
        height: 183px;
        box-sizing: border-box;
    }

    .paltan-flex {
        display: flex;

        >div {
            flex: 1;
        }
    }

    .small-margin {
        margin-bottom: 11px;
    }

    .mid-margin {
        margin-bottom: 22px;
    }

    .detail-card {
        padding: 22px 20px 25px 21px;
        box-sizing: border-box;
        height: 100px;

        .status-btn {
            width: 58px;
            height: 22px;
            letter-spacing: 3px;
            font-size: 12px;
            display: inline-block;
            background: #CEFFDC;
            border-radius: 4px;
            color: #00B63B;
            line-height: 22px;
            text-align: center;
            border: 1px solid #23B34C;
        }

        .pot-leve {
            width: 58px;
            height: 22px;
            letter-spacing: 3px;
            font-size: 12px;
            display: inline-block;
            background: #e95353;
            border-radius: 4px;
            color: #fff;
            line-height: 22px;
            text-align: center;
            border: 1px solid #e95353;
        }
    }
}










.plat-heda-cont {
    display: flex;
    height: 1.16rem;
    box-sizing: border-box;
    justify-content: space-between;
    padding: 0.22rem 0.22rem 0.19rem 0.24rem;
}

.plat-heda-cont>div {
    display: flex;
    flex-direction: column;
}

.plat-heda-cont>div:last-child>div:nth-child(2) {
    width: 1.87rem;
}

.plat-heda-cont>div>div:first-child {
    font-size: 0.14rem;
    color: #909cb1;
    margin-bottom: 0.15rem;
}

.plat-heda-cont>div>div:nth-child(2n) {
    font-size: 0.16rem;
    color: #333;
}

.line-card {
    padding: 0.35rem 0.35rem 0 0;
}

.needLine {
    height: 1.73rem;
}
